.container {
    position: relative;
    display: flex;
    justify-items: center;
    justify-content: center;
    align-items: center;
    max-width: 100vw;
    flex-wrap: wrap;
    margin: 40px 0;
}

.container .characters-list {
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(
      auto-fit,
      minmax(200px, 1fr)
    );
    list-style: none;
}

  .container .characters-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
.container .characters-list .card {
    position: relative;
    width: 280px;
    height: 400px;
    margin: 30px;
    box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
    border-radius: 15px;
    background: rgba(255, 255, 255, 0.1);
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    border-top: 1px solid rgba(255, 255, 255, 0.5);
    border-left: 1px solid rgba(255, 255, 255, 0.5);
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
}

.container .characters-list .card .content {
    padding: 20px;
    text-align: center;
}

.container .characters-list .card .content h2 {
    position: absolute;
    top: -80px;
    right: 30px;
    font-size: 8em;
    color: rgba(255, 255, 255, 0.05);
    pointer-events: none;
}

.container .characters-list .card .content h3 {
    font-size: 1.8em;
    color: #fff;
    z-index: 1;
}

.container .characters-list .card .content img {
    max-width: 50%;
    margin-top: 10px;
    border: 2px solid #fff;
}

.container .characters-list .card .content a {
    position: relative;
    display: inline-block;
    margin-top: 15px;
    padding: 8px 20px;
    background: linear-gradient(to left, #4776e6, #8e54e9);
    cursor: pointer;
    color: white;
    font-size: 0.9rem;
    font-weight: bold;
    border-radius: 20px;
    transition: all 0.3s ease;
    text-decoration: none;
}

.container .characters-list .card .content a:hover {
  letter-spacing: 0.5px;
  background: linear-gradient(to right, #4776e6, #8e54e9);
}
.main-container {
    background: rgba(255, 255, 255, 0.61);
    max-width: 500px;
    max-height: 10vh;
    padding: 2rem;
    box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.2);
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 25px
}

.input-container {
    display: flex;
    flex-direction: column;
}

.input-container .input-field {
    position: relative;
    margin-bottom: 1.5rem;
}

.input-container .input-field .underline::after {
  content: '';
  position: absolute;
  height: 1px;
  width: 100%;
  bottom: -5px;
  left: 0;
  background: linear-gradient(45deg, #8e2de2, #4a00e0);
  transform: scaleX(0);
  transition: all .3s ease-in-out;
  transform-origin: left;
}

.input-container .input-field input:focus ~ .underline::after {
  transform: scaleX(1);
}

.input-container .input-field input {
  outline: none;
  background: transparent;
  border: 0;
  font-size: 1rem;
  color: rgba(0, 0, 0, 0.7);
}

.input-container .input-field input::-webkit-input-placeholder {
  color: rgba(0, 0, 0, 0.5);
}

.input-container .input-field input:-ms-input-placeholder {
  color: rgba(0, 0, 0, 0.5);
}

.input-container .input-field input::-ms-input-placeholder {
  color: rgba(0, 0, 0, 0.5);
}

.input-container .input-field input::placeholder {
  color: rgba(0, 0, 0, 0.5);
}
